.hwall {
  width: 50px;
  height: 10px;
  background-color: #EEEEEE;
  position: absolute;
}

.hwall:hover {
  width: 120px;
  height: 10px;
  background-color: #DDDDDD;
  z-index: 1;
  -webkit-transition-delay: 0.1s;
       -o-transition-delay: 0.1s;
          transition-delay: 0.1s;
}

.hwall-player1 {
  width: 120px;
  height: 10px;
  background-color: #999999;
  z-index: 1;
  position: absolute;
}

.hwall-player2 {
  width: 120px;
  height: 10px;
  background-color: #999999;
  z-index: 1;
  position: absolute;
}

.vwall {
  width: 10px;
  height: 50px;
  background-color: #EEEEEE;
  position: absolute;
}

.vwall:hover {
  width: 10px;
  height: 120px;
  background-color: #DDDDDD;
  z-index: 1;
  -webkit-transform: rotate(180deg);
      -ms-transform: rotate(180deg);
          transform: rotate(180deg);
  bottom: 10px;
  -webkit-transition-delay: 0.1s;
       -o-transition-delay: 0.1s;
          transition-delay: 0.1s;
  /*transform-origin: top;*/
}

.vwall-player1 {
  width: 10px;
  height: 120px;
  background-color: #999999;
  z-index: 1;
  -webkit-transform: rotate(180deg);
      -ms-transform: rotate(180deg);
          transform: rotate(180deg);
  bottom: 10px;
  position: absolute;
  /*transform-origin: top;*/
}

.vwall-player2 {
  width: 10px;
  height: 120px;
  background-color: #999999;
  z-index: 1;
  -webkit-transform: rotate(180deg);
      -ms-transform: rotate(180deg);
          transform: rotate(180deg);
  bottom: 10px;
  position: absolute;
  /*transform-origin: top;*/
}



